import * as Actions from '../actions/movies';

const initialState = {
    popular: [],
    one: {},
    reccomendations: [],
    search_text: '',
    isFetched: false ,
};

export default (state = initialState, action) => {
    switch (action.type) {
        case Actions.LOAD_POPULAR_REQUEST:
            return {
                ...state,
                popular: {},
                isFetched: false
            };
        case Actions.LOAD_POPULAR_SUCCESS:
            return {
                ...state,
                popular: action.payload,
                isFetched: true
            };
        case Actions.LOAD_SEARCH_REQUEST:
            return {
                ...state,
                all: [],
                search_text: action.search_text,
                isFetched: false
            };
        case Actions.CLEAR_SEARCH_TEXT:
            return {
                ...state,
                search_text: ''
            };
        case Actions.LOAD_SEARCH_SUCCESS:
            return {
                ...state,
                all: action.payload,
                isFetched: true
            };
        default:
            return state
    }
};


