import api from '../services/api';

export const LOAD_POPULAR_REQUEST = 'LOAD_POPULAR_REQUEST';
export const LOAD_POPULAR_SUCCESS = 'LOAD_POPULAR_SUCCESS';
export const LOAD_POPULAR_ERROR = 'LOAD_POPULAR_ERROR';

export const LOAD_SEARCH_REQUEST = 'LOAD_SEARCH_REQUEST';
export const LOAD_SEARCH_SUCCESS = 'LOAD_SEARCH_SUCCESS';
export const LOAD_SEARCH_ERROR = 'LOAD_SEARCH_ERROR';

export const CLEAR_SEARCH_TEXT = 'CLEAR_SEARCH_TEXT';


export const LoadPopular = (pageNumber = 1) => {
    return (dispatch) => {
        dispatch(onLoadPopular.request());
        return onLoadPopular.fetch(pageNumber)
            .then(({ data }) => {
                dispatch(onLoadPopular.success(data));
            })
            .catch((error) => {
                dispatch(onLoadPopular.error(error))
            });
    }
};

const onLoadPopular = {
    request: () => ({
        type: LOAD_POPULAR_REQUEST
    }),
    fetch: (pageNumber) => {
        return api.request.get(`/movie/popular?page=${pageNumber}`);
    },
    success: (payload) => {
        return {
            type: LOAD_POPULAR_SUCCESS,
            payload
        }
    },
    error: (payload) => ({
        type: LOAD_POPULAR_ERROR,
        errors: payload
    })
};

export const LoadSearch = (query) => {
    return (dispatch) => {
        dispatch(onLoadSearch.request(query));
        return onLoadSearch.fetch(query)
            .then(({ data }) => {
                dispatch(onLoadSearch.success(data));
            })
            .catch((error) => {
                dispatch(onLoadSearch.error(error))
            });
    }
};

const onLoadSearch = {
    request: (query) => ({
        type: LOAD_SEARCH_REQUEST,
        search_text: query,
    }),
    fetch: (query) => {
        return api.request.get(`/search/movie?query=${query}`);
    },
    success: (payload) => {
        return {
            type: LOAD_SEARCH_SUCCESS,
            payload
        }
    },
    error: (payload) => ({
        type: LOAD_SEARCH_ERROR,
        errors: payload
    })
};


export const ClearSearchText = () => {
    return (dispatch) => {
        dispatch(onClearSearchText.request());
    }
};

const onClearSearchText = {
    request: () => ({
        type: CLEAR_SEARCH_TEXT
    })
};




