import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Pagination from 'react-js-pagination';

import { LoadPopular } from "../actions/movies";
import MovieList from "../components/MovieList";

class MainPage extends React.Component {

    static path = '/:page?';

    componentDidMount() {
        const {match, LoadPopular} = this.props;
        LoadPopular(match.params.page);
    }

    handlePageClick = (pageNumber) => {
        const {history, LoadPopular} = this.props;
        LoadPopular(pageNumber);
        if(pageNumber > 1)
            history.push(`/${pageNumber}`);
        else
            history.push(`/`);
    };

    render() {
        const { popular, isFetched } = this.props;

        if(!isFetched)
            return (
                <div>
                    Loading...
                </div>
            );
        return(
            <div>
                <MovieList movies={popular.results}/>

                <Pagination
                    activePage={popular.page}
                    itemsCountPerPage={20}
                    totalItemsCount={popular.total_results}
                    pageRangeDisplayed={5}
                    onChange={this.handlePageClick}
                    prevPageText={``}
                    nextPageText={``}
                    firstPageText={``}
                    lastPageText={``}
                />
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => bindActionCreators(
    {
        LoadPopular,
    },
    dispatch
);

const mapStateToProps = (state) => {
    return {
        popular: state.movies.popular,
        isFetched: state.movies.isFetched,
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(MainPage);


