import api from '../services/api';

export const LOAD_DETAIL_REQUEST = 'LOAD_DETAIL_REQUEST';
export const LOAD_DETAIL_SUCCESS = 'LOAD_DETAIL_SUCCESS';
export const LOAD_DETAIL_ERROR = 'LOAD_DETAIL_ERROR';

export const LoadDetail = (id) => {
    return (dispatch) => {
        dispatch(onLoadDetail.request());
        return onLoadDetail.fetch(id)
            .then(({ data }) => {
                dispatch(onLoadDetail.success(data));
            })
            .catch((error) => {
                dispatch(onLoadDetail.error(error))
            });
    }
};

const onLoadDetail = {
    request: () => ({
        type: LOAD_DETAIL_REQUEST
    }),
    fetch: (id) => {
        return api.request.get(`/movie/${id}`);
    },
    success: (payload) => {
        return {
            type: LOAD_DETAIL_SUCCESS,
            payload
        }
    },
    error: (payload) => ({
        type: LOAD_DETAIL_ERROR,
        errors: payload
    })
};




