import api from '../services/api';

export const LOAD_TRAILER_REQUEST = 'LOAD_TRAILER_REQUEST';
export const LOAD_TRAILER_SUCCESS = 'LOAD_TRAILER_SUCCESS';
export const LOAD_TRAILER_ERROR = 'LOAD_TRAILER_ERROR';

export const LoadTrailer = (movie_id) => {
    return (dispatch) => {
        dispatch(onLoadTrailer.request());
        return onLoadTrailer.fetch(movie_id)
            .then(({ data }) => {
                dispatch(onLoadTrailer.success(data));
            })
            .catch((error) => {
                dispatch(onLoadTrailer.error(error))
            });
    }
};

const onLoadTrailer = {
    request: () => ({
        type: LOAD_TRAILER_REQUEST
    }),
    fetch: (movie_id) => {
        return api.request.get(`/movie/${movie_id}/videos`);
    },
    success: (payload) => {
        return {
            type: LOAD_TRAILER_SUCCESS,
            payload
        }
    },
    error: (payload) => ({
        type: LOAD_TRAILER_ERROR,
        errors: payload
    })
};




