import React from 'react';
import { withRouter } from 'react-router-dom';
import { LoadSearch } from '../actions/movies';
import {bindActionCreators} from "redux";
import {connect} from "react-redux";

class Header extends React.Component {

    handleInputSearch = (e) => {
        const { history } = this.props;
        let value = e.target.value;
        if (value.length > 0) {
            history.push(`/search/${value}`);
        } else {
            history.push(`/`);
        }
    };

    render() {

        const { search_text } = this.props;

        return(
            <nav>
                <ul>
                    <li><img src="/logo.svg" alt="" className="logo"/></li>
                    <li><a href="#">Movies</a></li>
                    <li><a href="#">TV Seasons</a></li>
                    <li><a href="#">TV Episodes</a></li>
                    <li><a href="#">People</a></li>
                </ul>
                <div className="search-bar">
                    <input type="text" placeholder="Search" onInput={this.handleInputSearch} value={search_text}/>
                    <button type="submit"></button>
                </div>
            </nav>
        )
    }
};

const mapDispatchToProps = (dispatch) => bindActionCreators(
    {
        LoadSearch
    },
    dispatch
);

const mapStateToProps = (state) => {
    return {
        search_text: state.movies.search_text,
        isFetched: state.movies.isFetched
    };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Header));


