import React from 'react';
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { LoadTrailer } from "../actions/trailer";
import { withRouter } from 'react-router-dom';
import { Loader } from 'react-loaders';

class Trailer extends React.Component {

    componentDidMount() {
        const { LoadTrailer, match } = this.props;
        LoadTrailer(match.params.movie_id);
    }

    render() {

        const { trailer, isFetched } = this.props;
        console.log(trailer)

        if(!isFetched)
            return (
                <Loader type="ball-grid-pulse" active/>
            );

        return (
            <div>
                {
                    trailer.results.length > 0 && (<iframe width="894" height="503" src={`https://www.youtube.com/embed/${trailer.results.shift().key}`} frameBorder="0" allow="autoplay; encrypted-media" allowFullScreen></iframe>)
                }
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => bindActionCreators(
    {
        LoadTrailer
    },
    dispatch
);

const mapStateToProps = (state) => {
    return {
        trailer: state.trailer.all,
        isFetched: state.trailer.isFetched
    };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Trailer));


