import React from 'react';
import { Link } from 'react-router-dom';

import config from '../config';
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {LoadGenres} from "../actions/genres";

class Movie extends React.Component {

    componentDidMount() {
        const { LoadGenres } = this.props;
        LoadGenres();
    }

    render() {

        const { movie, isFetched, genres } = this.props;

        return (
            <div className="movie">
                <Link to={`/movieInfo/${movie.id}`} className="movie-poster">
                    <img src={`${config.API_IMAGE.small}/${movie.poster_path}`} alt="" />
                    <p className="movie-rating">{movie.vote_average}</p>
                </Link>
                <div className="movie-info">
                    <Link to={`/movieInfo/${movie.id}`} className="movie-name">{movie.title}</Link>
                    <div className="movie-genres">
                        {isFetched && movie.genre_ids.map((id, index) => {
                            const item = genres.filter(genre => genre.id === id);
                            if(item.length)
                                return (
                                    <a href="#" className="genre">{isFetched && item[0].name}</a>
                                )
                        })}
                    </div>
                </div>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => bindActionCreators(
    {
        LoadGenres
    },
    dispatch
);

const mapStateToProps = (state) => {
    return {
        genres: state.genres.all,
        isFetched: state.movies.isFetched
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Movie);


