import * as Actions from '../actions/movie';

const initialState = {
    detail: {},
    isFetched: false,
};

export default (state = initialState, action) => {
    switch (action.type) {
        case Actions.LOAD_DETAIL_REQUEST:
            return {
                ...state,
                detail: {},
                isFetched: false
            };
        case Actions.LOAD_DETAIL_SUCCESS:
            return {
                ...state,
                detail: action.payload,
                isFetched: true
            };
        default:
            return state
    }
};


