import React from 'react';
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { LoadDetail } from "../actions/movie";
import { LoadRecommendations } from "../actions/recommendations";
import RecommendationsList from '../components/RecommendationsList';
import Trailer from '../components/Trailer';
import config from "../config";
import { Loader } from 'react-loaders';

class MoviePage extends React.Component {

    static path = '/movieInfo/:movie_id';

    componentDidMount() {
        const {match, LoadDetail, LoadRecommendations} = this.props;
        LoadDetail(match.params.movie_id);
        LoadRecommendations(match.params.movie_id);
    }

    componentWillReceiveProps(nextProps) {
        const { match, LoadDetail, LoadRecommendations} = this.props;
        if(nextProps.match.params.movie_id && match.params.movie_id !== nextProps.match.params.movie_id) {
            LoadDetail(nextProps.match.params.movie_id);
            LoadRecommendations(nextProps.match.params.movie_id);
        }
    }

    render() {

        const { detail, isFetched, recommendations} = this.props;

        if(!isFetched)
            return (
                <Loader type="ball-grid-pulse" active/>
            );

        return (
            <div>
                <div className="movie-page">
                    <div>
                        <img src={`${config.API_IMAGE.small}/${detail.poster_path}`} alt="" className="single-movie-poster"/>
                        <h4>IMDB ID:{detail.imdb_id}</h4>
                        <h4>Original title:{detail.original_language}</h4>
                        <h4>{detail.tagline}</h4>
                    </div>

                    <div className="movie-description">
                        <h2>{detail.title}</h2>
                        <p className="movie-plot">{detail.overview}</p>
                        <h4>Trailer:</h4>
                        <Trailer/>
                    </div>
                </div>
                <RecommendationsList movies={recommendations.results}/>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => bindActionCreators(
    {
        LoadDetail,
        LoadRecommendations,
    },
    dispatch
);

const mapStateToProps = (state) => {
    return {
        detail: state.movie.detail,
        recommendations: state.recommendations.all,
        isFetched: state.movie.isFetched,
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(MoviePage);


